<template>
    <section class="account">
        <div class="title"></div>
        <div class="password">
            <i class="fa fa-key fa-fw"></i>
            <input type="password"
                   placeholder=""
                   v-model="pw">
            <br>
            <i class="fa fa-key fa-fw"></i>
            <input type="password"
                   placeholder=""
                   v-model="rpw">
        </div>
        <div class="panel">
            <button @click="savePw"></button>
        </div>
    </section>
</template>
<script>
    import {userName}   from '../vuex/getters'
    import {pop}        from '../vuex/actions'
    export default{
        data(){
            return {
                pw: '',
                rpw: ''
            }
        },
        methods: {
            savePw(){
                if (this.userName === '') {
                    this.pop({
                        pop: true,
                        content: '',
                        cb1: function () {
                            this.pop({})
                        }.bind(this)
                    })
                    return
                }
                if (this.pw === this.rpw) {
                    this.$http.post('/savePw', {
                        userName:this.userName,
                        password:this.pw
                    }).then(()=> {
                        this.pop({
                            pop: true,
                            content: '',
                            cb1: function () {
                                this.pop({})
                            }.bind(this)
                        })
                    }, (response)=> {
                        console.log(response)
                    })
                }else {
                    this.pop({
                        pop: true,
                        content: '',
                        cb1: function () {
                            this.pop({})
                        }.bind(this)
                    })
                }
            }
        },
        vuex: {
            getters: {
                userName
            },
            actions: {
                pop
            }
        }
    }
</script>
<style lang="sass">
    @import "../style/components/Account.scss";
</style>


